<template>
  <div class="record">
    <div class="handeRefund">
      <a href="javascript:;" @click="handeRefund">退款</a>
    </div>
    <van-tabs v-model="active" swipeable>
      <van-tab v-show="active==0" title="充值明细">
        <van-list v-model="loading" :finished="finished" finished-text="没有更多内容" @load="onLoad(0)">
          <!-- <van-cell v-for="item in list" :key="item" :title="item"/> -->
          <div class="recordItem" v-for="(item,i) in list" :key="item" :title="item">
            <div class="boxTop">
              <div>
                <h6>充值门店-青年城店</h6>
                <div
                  class="cardType"
                  v-show="i%2==0"
                  style="color: rgba(131, 145, 168, 1);background: linear-gradient(270deg,rgba(170, 182, 202, 1) 0%,rgba(240, 243, 254, 1) 100%
        );"
                >银卡</div>
                <div
                  class="cardType"
                  v-show="i%2!=0"
                  style="background:linear-gradient(270deg,rgba(194,157,96,1) 0%,rgba(219,209,172,1) 100%);color:rgba(138,86,30,1);"
                >金卡</div>
              </div>
              <div class="balaces">+187.94</div>
            </div>
            <div class="boxBottom">
              <div class="time">2019-04-28</div>
              <div class="balance">卡余额: ￥1123.00</div>
            </div>
          </div>
        </van-list>
      </van-tab>
      <van-tab v-show="active==1" title="消费明细" finished-text="没有更多了">
        <van-list v-model="loading2" :finished="finished2" finished-text="没有更多内容" @load="onLoad(1)">
          <!-- <van-cell v-for="item in list" :key="item" :title="item"/> -->
          <div class="recordItem" v-for="(item,i) in list2" :key="item" :title="item">
            <div class="boxTop">
              <div>
                <h6>消费门店-青年城店</h6>
                <div
                  class="cardType"
                  v-show="i%2==0"
                  style="color: rgba(131, 145, 168, 1);background: linear-gradient(270deg,rgba(170, 182, 202, 1) 0%,rgba(240, 243, 254, 1) 100%
        );"
                >银卡</div>
                <div
                  class="cardType"
                  v-show="i%2!=0"
                  style="background:linear-gradient(270deg,rgba(194,157,96,1) 0%,rgba(219,209,172,1) 100%);color:rgba(138,86,30,1);"
                >金卡</div>
              </div>
              <div class="balaces">-187.94</div>
            </div>
            <div class="boxBottom">
              <div class="time">2019-04-28</div>
              <div class="balance">卡余额: ￥1123.00</div>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
    <div class="van-list__finished-text">只展示最近半年的交易记录</div>
  </div>
</template>
<script>
import { Tab, Tabs, List, Cell,Toast } from "vant";
// Vue.use(PasswordInput).use(NumberKeyboard);
export default {
  components: {
    [Tab.name]: Tab,
    [Tabs.name]: Tabs,
    [List.name]: List,
    [Cell.name]: Cell,
    [Toast.name]: Toast
  },
  data() {
    return {
      refundPhone:'152451254125',
      active: 0,
      list: [],
      loading: false,
      finished: false, //到达底部标识
      list2: [],
      loading2: false,
      finished2: false //到达底部标识
    };
  },
  mounted() {
    // this.$http.get("/memberLs/getUserInfo", function(res) {
    //   console.log(res);
    // }); // location.href="http://yycms.test.yyhealth.com/admin/memberLs/getUserInfo"
  },
  methods: {
    onLoad(type) {
      switch (type) {
        case 0: //充值明细
          // 异步更新数据
          setTimeout(() => {
            for (let i = 0; i < 5; i++) {
              this.list.push(this.list.length + 1);
            }
            // 加载状态结束
            this.loading = false;
            // 数据全部加载完成
            if (this.list.length >= 20) {
              this.finished = true;
            }
          }, 2000);
          console.log("充值");
          break;
        case 1: //消费明细
          // 异步更新数据
          setTimeout(() => {
            for (let i = 0; i < 5; i++) {
              this.list2.push(this.list2.length + 1);
            }
            // 加载状态结束
            this.loading2 = false;
            // 数据全部加载完成
            if (this.list2.length >= 20) {
              this.finished2 = true;
            }
          }, 2000);
          console.log("消费");
          break;
      }
    },
    handeRefund(){
        Toast('请联系集团管理员 '+this.refundPhone);
    }
  }
};
</script>

